/* для экранов от 1200px*/
@media (min-width: 1200px) {
.shop {
border: 1px solid black;
width: 60%;
margin: 10px 20% 0px 20%;

}

h1 {
  text-align: center;
  background-color: orangered;
  color: white;
}

.card {
    border: 1px solid black;
    margin: 0px 20px 10px 20px;
    padding: 5px;
}
.vitrine {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

b {
  color: green;
  font-weight: bold;
}

.promo {
  padding: 10px;
}

input {
  margin-right: 5px;
}
}


/*экраны до 480px*/
@media (max-width: 480px) {
.shop {
border: 1px solid black;
width: 96%;
margin: auto;
margin-top: 10px;
margin-bottom: 10px;
font-size: 10px;

}

h1 {
  text-align: center;
  background-color: orangered;
  color: white;
}

.card {
    border: 1px solid black;
    margin: 0px 20px 10px 20px;
    padding: 5px;
    font-size: 12px;
}
.vitrine {
    display: block;
    grid-template-columns: 1fr 1fr;
}

b {
  color: green;
  font-weight: bold;
}

.promo {
  padding: 10px;
  display: flex;
  align-items: center;
}

input {
  margin-right: 5px;
  width: 40%;
  height: 14px;
}

button {
  font-size: 11px;
}
}

